











import Vue from 'vue'
import { mapState } from 'vuex'

export default Vue.extend({
  computed: {
    ...mapState(['schooltimetable'])
  },
  methods: {
    addForm(e) {
      const newFormName: string = e.target.value
      this.$store.dispatch('creatForm', { newFormName })
      e.target.value = ''
    }
  }
})
